.app {
  height: 100vh;
  overflow: auto;
  padding-bottom: 4.5rem;
}
.footer {
  width: 100%;
  position: fixed;
  bottom: 0;
  left: 0;
  height: 4rem;
  background: rgba(255,255,255,1);
  -webkit-box-shadow: 0 2px 20px 0 #b3b3b3;
          box-shadow: 0 2px 20px 0 #b3b3b3;
}
.footer > input {
  width: 83%;
  height: 100%;
  border: 0;
  outline: none;
  font-size: 1.2rem;
  padding-left: 2%;
  font-weight: 400;
}
.footer > .send {
  display: inline-block;
  border-radius: 50%;
  background: #C3A5FE;
  width: 3rem;
  height: 3rem;
  text-align: center;
  line-height: 3rem;
  font-weight: 500;
  float: right;
  margin-top: 0.5rem;
  color: #fff;
  cursor: pointer;
  margin-right: 2.5%;
}
.footer > .send:hover {
  opacity: 0.9;
}
.red {
  color: #e20f0f;
}
.clearfix:after {
  display: block;
  clear: both;
  content: "";
  visibility: hidden;
  height: 0;
}
li {
  list-style: none;
}
.li-right {
  position: relative;
  padding: 0.833rem 0;
}
.li-right > .text {
  float: right;
  background: #fff;
  border-radius: 4rem;
  padding: 0.833rem 1.25rem;
  display: inline-block;
  max-width: 75%;
  word-break: break-all;
  font-size: 1.13rem;
  font-weight: 400;
}
.li-right > .user-name {
  float: right;
  font-size: 1.25rem;
  font-weight: 400;
  margin-left: 0.833rem;
}

.li-left {
  position: relative;
  padding: 0.833rem 0;
}
.li-left > .text {
  float: left;
  background: #169e32;
  color: #fff;
  border-radius: 4rem;
  padding: 0.833rem 1.25rem;
  display: inline-block;
  max-width: 75%;
  word-break: break-all;
  font-size: 1.13rem;
  font-weight: 400;
}
.li-left > .other-name {
  float: left;
  font-size: 1.25rem;
  font-weight: 400;
  margin-right: 0.833rem;
}


