body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}
body, html {
  font-size: 12px;
}
#root {
  height: 100vh;/* FF3.6+ */
  background: -webkit-gradient(linear, 223deg, color-stop(17%, 6cbff6), color-stop(64%, C3A5FE));/* Chrome,Safari4+ */
  background: -webkit-linear-gradient(223deg, #6cbff6 17%, #C3A5FE 64%);/* Chrome10+,Safari5.1+ */
  background: -o-linear-gradient(223deg, #6cbff6 17%, #C3A5FE 64%);/* Opera 11.10+ */
  background: -ms-linear-gradient(223deg, #6cbff6 17%, #C3A5FE 64%);/* IE10+ */
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#1301FE', endColorstr='#F4F60C', GradientType='1'); /* for IE */
  background: -webkit-linear-gradient(227deg, #6cbff6 17%, #C3A5FE 64%);
  background: -o-linear-gradient(227deg, #6cbff6 17%, #C3A5FE 64%);
  background: linear-gradient(223deg, #6cbff6 17%, #C3A5FE 64%);/* W3C */
}



