.login {
  position: fixed;
  z-index: 10;
  left: 0;
  top: 0;
  background: rgba(255, 255, 255, 1);
  width: 100%;
  height: 100%;
}
.login > .form {
  width: 60%;
  background: #fff;
  margin-left: 20%;
  text-align: center;
  border-radius: 0.25rem;
  padding: .25%;
  position: absolute;
  top: 30%;
  -webkit-box-shadow: 0 0 0  9px #e0c81b;
          box-shadow: 0 0 0  9px #e0c81b;
}
.login > .form p {
  margin-top: 0;
  font-weight: 400;
  font-size: 1.5rem;
  text-shadow: 6px 4px 10px #e00c0c;
}
.login > .form input {
  outline: none;
  width: 100%;
  padding: 0.5rem 0;
  border: 0;
  text-align: center;
  font-size: 1.2rem;
  font-weight: 400;
  border-bottom: 1px solid #eee;
}


